import React, { useState } from 'react';
import { Card, Form, InputNumber, Radio, Button, Typography, Space } from 'antd';

const { Title, Text } = Typography;

const LiquidationCalculator = () => {
  const [liquidationPrice, setLiquidationPrice] = useState(null);

  const calculateLiquidationPrice = (values) => {
    const { openPrice, leverage, direction, initialMargin, additionalMargin = 0, feeRate = 0.0006 } = values;
    const totalMargin = initialMargin + additionalMargin;
    
    const positionSize = initialMargin * leverage / openPrice;
    const totalFee = positionSize * openPrice * feeRate * 2;
    
    let price;
    if (direction === 1) {
      price = (positionSize * openPrice - totalMargin + totalFee) / positionSize;
    } else {
      price = (positionSize * openPrice + totalMargin - totalFee) / positionSize;
    }
    
    setLiquidationPrice(price.toFixed(2));
  };

  return (
    <Card>
      <Form
        layout="vertical"
        onFinish={calculateLiquidationPrice}
        initialValues={{
          leverage: 5,
          direction: -1,
          initialMargin: 10,
          additionalMargin: 0,
          feeRate: 0.0006,
        }}
      >
        <Form.Item
          label="å¼€ä»“ä»·"
          name="openPrice"
          rules={[{ required: true, message: 'è¯·è¾“å…¥å¼€ä»“ä»·' }]}
        >
          <InputNumber style={{ width: '100%' }} placeholder="è¯·è¾“å…¥å¼€ä»“ä»·" />
        </Form.Item>

        <Form.Item label="å¼€ä»“æ æ†" name="leverage">
          <InputNumber style={{ width: '100%' }} min={1} max={125} />
        </Form.Item>

        <Form.Item label="å¼€ä»“æ–¹å‘" name="direction">
          <Radio.Group>
            <Radio value={1}>åšå¤š</Radio>
            <Radio value={-1}>åšç©º</Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item label="åˆå§‹ä¿è¯é‡‘" name="initialMargin">
          <InputNumber style={{ width: '100%' }} min={0} />
        </Form.Item>

        <Form.Item label="è¿½åŠ ä¿è¯é‡‘" name="additionalMargin">
          <InputNumber style={{ width: '100%' }} min={0} />
        </Form.Item>

        <Form.Item label="æ‰‹ç»­è´¹çŽ‡" name="feeRate">
          <InputNumber style={{ width: '100%' }} min={0} max={1} step={0.0001} />
        </Form.Item>

        <Form.Item>
          <Button type="primary" htmlType="submit">
            è®¡ç®—çˆ†ä»“ä»·
          </Button>
        </Form.Item>

        {liquidationPrice && (
          <Space direction="vertical" style={{ marginTop: 16 }}>
            <Text strong>çˆ†ä»“ä»·æ ¼ï¼š</Text>
            <Title level={3} type="danger">
              {liquidationPrice}
            </Title>
          </Space>
        )}
      </Form>
    </Card>
  );
};

export default LiquidationCalculator;
